@import url(https://fonts.googleapis.com/css?family=Rajdhani&display=swap);
body {
  
  background: rgb(42, 34, 25);
  /* background: radial-gradient(circle, rgb(255, 126, 6) 0%, rgb(255, 136, 0) 41%, rgb(255, 104, 4) 80%, rgb(255, 132, 1) 96%); */
  margin: 0;
  font-family: 'Rajdhani', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.center{
  display: flex;
  justify-content: center;
}

.bounding{
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}

.absolute{
    position: absolute;
}

.imgcenter {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
